<template>
    <div id='App'>
        <h1> To do list </h1>

        <ul aria-labelledby='list-summary' class='stack-large'>
            <li v-for="item in ToDoItems" :key="item.id">
                <to-do-item :label="item.label" :done="item.done" :id="item.id" @checkbox-changed='updateDoneStatus(item.id)' @item-deleted="deleteToDo(item.id)" @item-edited="editToDo(item.id, $event)"></to-do-item>
            </li>
        </ul>

        <h2 class='list-summary'> {{listSummary}} </h2>

        <to-do-form @todo-added='addToDo'></to-do-form>

        <!--<form action="https://tmacedo.pythonanywhere.com/cim/updatejson" method="post" enctype="multipart/form-data">
            <input type="file" name="jsonfile"/>
            <button type="submit" class="btn"> Save </button>
        </form>-->
        
        <form action="https://tmacedo.pythonanywhere.com/cim/test" method="post" enctype="application/json">
            <input type="hidden" id="hiddenInput" name="jsondata"/>
            <button type="submit" class="btn" @click='sendData(this.ToDoItems)'> Send </button>
        </form>
    </div>
</template>

<script>
    import ToDoItem from "./components/ToDoItem.vue";
    import uniqueId from "lodash.uniqueid";
    import ToDoForm from "./components/ToDoForm.vue";
    import jsonData from "./assets/json_data.json"

    export default {
        name: 'App',
        
        components: { ToDoItem, ToDoForm },

        data () { 
            return {
                ToDoItems: jsonData
            } 
        },

        methods: {
            addToDo (toDoLabel) {
                this.ToDoItems.push({ id: uniqueId("todo-"), label: toDoLabel, done: false});
            },
            updateDoneStatus (itemId) {
                const itemToUpdate = this.ToDoItems.find((item) => item.id === itemId);
                itemToUpdate.done = !itemToUpdate.done;
                console.log(itemToUpdate, itemToUpdate.done);
            },
            deleteToDo (itemId) {
                const itemIndex = this.ToDoItems.findIndex((item) => item.id === itemId);
                this.ToDoItems.splice(itemIndex, 1);
            },
            editToDo (itemId, newLabel) {
                const itemToEdit = this.ToDoItems.find((item) => item.id === itemId);
                itemToEdit.label = newLabel;
            },
            sendData (stuff) {
                const outData = JSON.stringify(stuff);
                document.getElementById('hiddenInput').value = outData;
            }
        },

        computed: {
            listSummary () {
                const numberItems = this.ToDoItems.length;
                const numberDoneItems = this.ToDoItems.filter((item) => item.done).length;
                return `Total: ${numberDoneItems} out of ${numberItems}`;
            }
        }
    };
</script>

